import React, { Component } from 'react';
import chroma from 'chroma-js';

class ColorWheel extends Component {
  constructor(props) {
    super(props);

    this.myRef = React.createRef()

    console.log('hey there, this is colorwheel');
    console.log('cwtype = ' + props.cwtype);
    console.log(props);
  }

  componentDidMount() {
    this.go();
  }

  componentWillUnmount() {
  }

  go() {
    let cmap = this.myRef.current;
    console.log('cmap = ' + cmap);
    console.log(cmap);

    // typical human sensitivities to different colors
    var s1 = { 'blue': 0.114 / 0.114
              , 'green': 0.587 / 0.114
              , 'red': 0.299 / 0.114
             };

    var s = { 'blue': 0.2 / 0.114
              , 'green': 0.2 / 0.114
              , 'red': 0.2 / 0.114
             };

    var lightness = function(r, g, b) {
        return s.red*r*r + s.green*g*g + s.blue*b*b;
    };

   var arc_coords = function(cent_x, cent_y, radius, theta) {
        var x = cent_x + Math.sin(theta)*radius;
        var y = cent_y - Math.cos(theta)*radius

        return {'x': x, 'y': y};
    };

    var get_gray = function(nval) {
        // nval -- normalized value, [0, 1)
        nval = Math.sqrt(nval);
        var inten = Math.round(nval * 255).toString(16);
        if (inten.length < 2) inten = '0' + inten;
        
        var color = '#' + inten + inten + inten;
        return color;
    };

    var get_cyclic1 = function(inten) {
        var red = '00';
        var green = '00';
        var blue = '00';
        
        if (inten < 1.0/3.0) {
            // interpolate between red and green
            var frac = inten * 3.0;
            green = Math.round(frac*255).toString(16);
            red = Math.round((1.0-frac)*255).toString(16);
        } else if (inten < 2.0/3.0) {
            // interpolate between green and blue
            var frac = (inten-1.0/3.0) * 3.0;
            blue = Math.round(frac*255).toString(16);
            green = Math.round((1.0-frac)*255).toString(16);
        } else {
            // interpolate between blue and red
            var frac = (inten-2.0/3.0) * 3.0;
            red = Math.round(frac*255).toString(16);
            blue = Math.round((1.0-frac)*255).toString(16);
        }

        if (red.length < 2)   red   = '0' + red;
        if (green.length < 2) green = '0' + green;
        if (blue.length < 2)  blue  = '0' + blue;

        return '#' + red + green + blue;
    };

    var get_cyclic2 = function(inten) {
        var red = '00';
        var green = '00';
        var blue = '00';
        
        if (inten < 1.0/3.0) {
            // interpolate between red and green
            var frac = inten * 3.0;
            var l = lightness(1.0-frac, frac, 0);

            var g = frac/Math.sqrt(l);
            var r = (1.0-frac)/Math.sqrt(l);
            green = Math.round(g*255).toString(16);
            red = Math.round(r*255).toString(16);
        } else if (inten < 2.0/3.0) {
            // interpolate between green and blue
            var frac = (inten-1.0/3.0) * 3.0;
            var l = lightness(0, 1.0-frac, frac);

            var b = frac/Math.sqrt(l);
            var g = (1.0-frac)/Math.sqrt(l);        
            blue = Math.round(b*255).toString(16);
            green = Math.round(g*255).toString(16);
        } else {
            // interpolate between blue and red
            var frac = (inten-2.0/3.0) * 3.0;
            var l = lightness(frac, 0, 1.0-frac);

            var r = frac/Math.sqrt(l);
            var b = (1.0-frac)/Math.sqrt(l);        
            red = Math.round(r*255).toString(16);
            blue = Math.round(b*255).toString(16);
        }

        if (red.length < 2)   red   = '0' + red;
        if (green.length < 2) green = '0' + green;
        if (blue.length < 2)  blue  = '0' + blue;

        return '#' + red + green + blue;
    };

    var get_cyclic3 = function(inten) {
      var red = 0;
      var green = 0;
      var blue = 0;

      function interp(val) {
        let sval = Math.sin(val*Math.PI/2.0);
//        sval = (sval + 2*val) / 3.0;
        return Math.round(sval*255);
//        return Math.round(Math.sqrt(sval)*255);
//        return Math.round(Math.pow(sval, 1.0)*255);
//        let rval = Math.round(Math.sin(val*Math.PI/2.0)*255);
//        console.log('rval = ' + rval + ' ' + val + ' ' + val*Math.PI);
//        return rval;
//        return Math.round(val*val*255);

//        return Math.round(Math.pow(val, 0.6)*255);
//        return Math.round(val*255);
      }

      if (inten < 1.0/6.0) {
        red = 255;
        blue = 0;

        let frac = inten * 6.0;
        green = interp(frac);
      } else if (inten < 2.0/6.0) {
        green = 255;
        blue = 0;

        let frac = (inten-1.0/6.0) * 6.0;
        red = interp(1.0-frac);
      } else if (inten < 3.0/6.0) {
        green = 255;
        red = 0;

        let frac = (inten-2.0/6.0) * 6.0;
        blue = interp(frac);
      } else if (inten < 4.0/6.0) {
        blue = 255;
        red = 0;

        let frac = (inten-3.0/6.0) * 6.0;
        green = interp(1.0-frac);
      } else if (inten < 5.0/6.0) {
        blue = 255;
        green = 0;

        let frac = (inten-4.0/6.0) * 6.0;
        red = interp(frac);
      } else {
        red = 255;
        green = 0;

        let frac = (inten-5.0/6.0) * 6.0;
        blue = interp(1.0-frac);
      }
      
      red = red.toString(16);
      green = green.toString(16);
      blue = blue.toString(16);

      if (red.length < 2)   red   = '0' + red;
      if (green.length < 2) green = '0' + green;
      if (blue.length < 2)  blue  = '0' + blue;
      
      return '#' + red + green + blue;
    };

    var get_cyclic4 = function(nval) {
      // nval -- normalized value, [0, 1)
      nval = nval * 2;
      if (nval >= 1.0) nval = 2.0-nval;

      var inten = Math.round(nval * 255).toString(16);
      if (inten.length < 2) inten = '0' + inten;
      
      var color = '#' + inten + inten + inten;
      return color;    
    }

    var get_cyclic5 = function(nval) {
      // nval -- normalized value, [0, 1)
      nval = nval * 2;
      if (nval < 1.0) {
//        nval = Math.sqrt(nval);
        let scale = chroma.scale(['black','red','white']).correctLightness();
        return scale(nval);
      } else {
        nval = 2.0-nval;
//        nval = Math.sqrt(nval);
        let scale = chroma.scale(['black','blue','white']).correctLightness();
        return scale(nval);
      }
    }

    var make_arc = function(cent_x, cent_y, radius1, radius2, theta1, theta2) {
      let width = radius2 - radius1;
      radius1 = radius1 + width/2.0;

      var coords1 = arc_coords(cent_x, cent_y, radius1, theta1);
      var coords2 = arc_coords(cent_x, cent_y, radius1, theta2);
      
      var path = document.createElementNS('http://www.w3.org/2000/svg',
                                          'path');
      
      var large_arc = '0'
      if (Math.abs(theta1-theta2) > Math.PI/2) large_arc = 1;
      
      var spec = 'M ' + coords1.x + ' ' + coords1.y + ' ';
      spec += 'A ' + radius1 + ' ' + radius1 + ' 0 ' + large_arc + ' 1 ';
      spec += coords2.x + ' ' + coords2.y;
      
      path.setAttribute('d', spec);
      path.setAttribute('stroke', 'green');
      path.setAttribute('stroke-width', width);
      path.setAttribute('fill', 'none');
      
      return path;
    }


    var make_wheel = function(cmap, f_color, inner=30, outer=100) {
        var narc = 300;
        for (var i=0; i<narc; ++i) {
            var end = i+2;
            if (i == narc-1) end = i+1;
            
            var theta1 = Math.PI*2/narc * i;
            var theta2 = Math.PI*2/narc * end;
            
            var arc = make_arc(100, 100, inner, outer, theta1, theta2);
            var color = f_color(i/narc);
            
            arc.setAttribute('stroke', color);
            cmap.appendChild(arc);
        }
    };

    var make_osc_wheel = function(cmap, f_color, inner=30, outer=100) {
        var narc = 500;
        for (var i=0; i<narc; ++i) {
            var end = i+2;
            if (i == narc-1) end = i+1;
            
            var theta1 = Math.PI*2/narc * i;
            var theta2 = Math.PI*2/narc * end;
            
            var arc = make_arc(100, 100, inner, outer, theta1, theta2);

            var wave = i/narc + Math.sin(i/narc * 2 * Math.PI * 100) * 0.01;
            if (wave < 0) wave = 1.0 + wave;
            if (wave > 1) wave = wave - 1.0;
            var color = f_color(wave);
            

            arc.setAttribute('stroke', color);
            cmap.appendChild(arc);
        }
    };

    if (this.props.type == "gray") make_wheel(cmap, get_gray);
    if (this.props.type == "cyclic1") make_wheel(cmap, get_cyclic1);
    if (this.props.type == "cyclic2") make_wheel(cmap, get_cyclic2);
    if (this.props.type == "cyclic3") {
      make_wheel(cmap, get_cyclic3, 30, 50);
      make_osc_wheel(cmap, get_cyclic3, 50, 100);
    }
    if (this.props.type == "cyclic4") {
      make_wheel(cmap, get_cyclic4, 30, 50);
      make_osc_wheel(cmap, get_cyclic4, 50, 100);
    }
    if (this.props.type == "cyclic5") {
      make_wheel(cmap, get_cyclic5, 30, 50);
      make_osc_wheel(cmap, get_cyclic5, 50, 100);
    }

  }

  render() {
    return (
      <div style={{display: 'block', margin: 'auto'}}>
        <svg width="200" height="200" 
             style={{display: 'block', margin: 'auto'}}
             ref={this.myRef}/>
      </div>
    );
  }
}

export default ColorWheel;



